.jokeBox {
    margin: 40px;
    background: rgb(238,174,202);
    background: radial-gradient(circle, rgba(238,174,202,1) 0%, rgba(148,187,233,1) 100%);
    color: #333;
    font-family: 'Courier New', Courier, monospace;
    font-size: 14px;
    line-height: 1.5;
    padding: 1rem;
    margin-bottom: 1rem;
    border-radius: 15px;
    overflow-x: auto;
    width: 400px;
    height: 200px;
    margin-top: 40px;
}